.bell-notification {
    color: #fff;
    position: relative;
  }

  .bell-notification::after {
    content: attr(count);
    position: absolute;
    top: 0;
    right:0;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: red;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    border: 1px solid #01111b;
  }